<template>
  <div class="form-check">
    <input
      class="form-check-input"
      type="checkbox"
      :name="name"
      :id="id"
      :checked="checked"
      @input="$emit('inputValue', $event.target.checked)"
    />
    <label :for="id" class="custom-control-label cursor-pointer">
      <slot />
    </label>
  </div>
</template>

<script>
  export default {
    name: 'component-checkbox',
    props: {
      name: String,
      id: String,
      checked: String,
    },
  };
</script>
